.navbar {
  animation: fadeInDown 1s;
  position: absolute;
  width: 100%;
}
.navbar-nav {
  gap: 1rem;
}
.navbar .nav-item .show {
  color: #ff9100 !important;
}

.dropdown-menu {
  border: none !important;
  border-radius: 0;
  padding: 0;
}
.dropdown-menu .dropdown-item {
  padding: 1rem;
}
.dropdown-menu .dropdown-item:hover {
  background-color: #ff9100;
  transition: all 3s ease-out;
}

/* Navbar fijo al hacer scroll */
.navbar-custom.fixed-top {
  background-color: rgba(0, 32, 96, 0.9);
  /* Se mantiene azul oscuro al hacer scroll */
}

/* Estilo inicial de la navbar (transparente con degradado) */
.navbar-custom {
  z-index: 1;
  transition: background-color 0.3s ease-in-out;
}

/* Estilo de la navbar cuando cambia al hacer scroll */
.navbar-scrolled {
  background-color: white !important;
  transition: background-color 0.4s ease;
}

/* Estilos de las letras y enlaces cuando la navbar es blanca */
.navbar-scrolled .nav-link {
  color: black !important;
}

/* Navbar blanco en dispositivos más pequeños */
@media (min-width: 992px) {
  .navbar-custom {
    background: linear-gradient(to bottom, rgba(0, 32, 96, 0.9), rgba(0, 32, 96, 0) 80%);
  }
}
@media (max-width: 992px) {
  .navbar-custom {
    background-color: white !important;
    color: #000;
  }
  .navbar-custom .nav-link {
    color: #000 !important;
  }
  .navbar-brand img {
    width: 180px;
    /* Ajustar el tamaño del logo en dispositivos móviles */
  }
  i {
    color: #000;
  }
}
/* Logo tamaño predeterminado */
.navbar-brand img {
  max-width: 200px;
  /* Tamaño predeterminado del logo */
}

/* Color de texto y ajuste de tamaños */
.navbar-custom .nav-link {
  color: #fff;
  font-size: 1.1rem;
  transition: color 0.3s ease-in-out;
}

.navbar-custom .nav-link:hover {
  color: #ffcc00;
}

.video-container {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  z-index: 0;
}

.video-container video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  /* Efecto de oscurecimiento */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  text-align: center;
  z-index: 10;
}

.welcome-text {
  font-size: 3rem;
  font-weight: 700;
  animation: fadeInDown 1s;
}

.action-button {
  margin-top: 20px;
  padding: 10px 30px;
  font-size: 1.2rem;
  border: none;
  border-radius: 5px;
  background-color: #FFC107;
  color: #000;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-decoration: none;
}

.action-button:hover {
  background-color: #ffae00;
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
