/*---------------------------------------------------------------------------------SECCION -3 DESTINOS DESTACADOS*/
/* Estilo de la sección de destinos destacados */
#destinosDestacados {
  padding: 60px 0; /* Aumenta el padding para mejor espaciado */
}

.destination {
  border-radius: 10px; /* Bordes redondeados */
  overflow: hidden; /* Ocultar el desbordamiento de la imagen */
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); /* Sombra sutil */
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Transición suave */
}

.destination:hover {
  transform: translateY(-5px); /* Levanta el contenedor en hover */
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2); /* Aumenta la sombra en hover */
}

.destination img {
  width: 100%;
  height: 300px; /* Mantén la altura fija */
  object-fit: cover; /* Cubre el área */
}

.destination h3 {
  margin-top: 15px; /* Margen superior para el título */
  font-weight: bold; /* Negrita para mayor énfasis */
}

.destination p {
  margin: 10px 0; /* Margen superior e inferior para la descripción */
}

/* Botón mejorado */
.btn-primary {
  background-color: #007bff; /* Cambia el color de fondo del botón */
  border: none; /* Sin borde */
  border-radius: 5px; /* Bordes redondeados */
  padding: 10px 20px; /* Relleno adecuado */
  transition: background-color 0.3s ease; /* Transición para el color de fondo */
}

.btn-primary:hover {
  background-color: #0056b3; /* Color más oscuro en hover */
}

.faq-item {
  border-bottom: 1px solid #ddd;
  padding: 10px 0;
}

.faq-answer {
  display: none;
  padding-top: 10px;
  animation: fadeIn 0.3s ease;
}

.toggle-icon {
  color: #007bff;
  border: none;
  font-size: 18px;
}

.faq-question {
  font-weight: bold;
  cursor: pointer;
}

.faq-section h2 {
  font-family: "Arial", sans-serif;
  color: #333;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/*---------------------------------------------------------------------------------SECCION 4 - TESTIMONIOS*/
/* Estilos para la sección de testimonios */
.testimonials-section {
  position: relative;
  height: 100vh; /* Altura de 80vh */
  overflow: hidden;
}
.testimonials-section .container {
  position: relative;
  z-index: 2;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.testimonials-background {
  background-image: url("../../..//public/images/web/images/destination-4.jpg"); /* Cambia la ruta a tu imagen de fondo */
  background-size: cover; /* Asegura que la imagen cubra toda la sección */
  background-position: center; /* Centra la imagen */
  position: absolute; /* Para que se superponga al contenido */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1; /* Envía la imagen de fondo detrás del contenido */
}

.testimonials-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); /* Capa de oscuridad */
  z-index: 2; /* Debe estar por encima del fondo pero debajo del contenido */
}

.testimonial {
  background: rgba(255, 255, 255, 0.9); /* Fondo blanco semitransparente para las tarjetas */
  border-radius: 10px; /* Bordes redondeados */
  padding: 30px; /* Espaciado interno */
  text-align: center; /* Texto centrado */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Sombra suave */
  transition: transform 0.3s; /* Animación suave al pasar el mouse */
}

.testimonial:hover {
  transform: scale(1.05); /* Escalado al pasar el mouse */
}
.testimonial:hover h4 {
  margin-top: 15px; /* Espaciado superior para los nombres */
  color: #007bff; /* Color para los nombres */
  font-weight: bold; /* Negrita para los nombres */
}

.testimonial-image {
  margin-bottom: 20px; /* Espacio inferior para las imágenes */
}

.testimonial-image img {
  outline: solid #ff9100 0.2rem;
  width: 80px; /* Ancho fijo para las imágenes */
  height: 80px; /* Alto fijo para las imágenes */
  object-fit: cover; /* Recorta la imagen para que se ajuste al contenedor */
}

.location-section .map-container {
  position: relative;
  width: 100%;
  height: 70vh;
  overflow: hidden;
}
.location-section .map-container iframe {
  width: 100%;
  height: 70vh;
  border: 0;
}
.location-section .map-container .location-info {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #1b50ff;
  color: #FFFFFF;
  padding: 20px;
  z-index: 1;
}
.location-section .map-container .location-info h3 {
  margin-bottom: 1rem;
}
.location-section .map-container .location-info .location-details p {
  margin-bottom: 0rem;
}
.location-section .map-container .location-info .location-details p i {
  margin-right: 0.5rem;
}
.location-section a {
  color: #FFFFFF;
  transition: color 0.3s;
}
.location-section a:hover {
  color: #71B100;
}

