@use 'base/variables';
.location-section {
    .map-container {
        position: relative; // Para posicionar la descripción sobre el mapa
        width: 100%;
        height: 70vh;
        overflow: hidden; // Evita que el contenido exceda el contenedor

        iframe {
            width: 100%;
            height: 70vh;
            border: 0;
        }

        .location-info {
            position: absolute; // Posiciona la descripción sobre el mapa
            bottom: 0; // Coloca la descripción en la parte inferior del mapa
            width: 100%; // Asegura que ocupe todo el ancho
            background-color: variables.$azul; // Color de fondo de la descripción
            color: variables.$blanco; // Color del texto
            padding: 20px; // Espaciado interno
            z-index: 1; // Asegura que esté encima del mapa

            h3 {
                margin-bottom: 1rem;
            }

            .location-details {
                p {
                    margin-bottom: 0rem;

                    i {
                        margin-right: 0.5rem;
                    }
                }
            }
        }
    }

    // Efecto hover para enlaces
    a {
        color: variables.$blanco; // Color del texto de los enlaces
        transition: color 0.3s; // Transición suave

        &:hover {
            color: variables.$color-hover; // Color del texto en hover
        }
    }
}
